@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@400;500;600;700&display=swap");
.header-container {
  position: relative;
  left: 0;
  padding: 5px;
  width: 100vw;
  height: 56px;
  display: flex;
  z-index: 10;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-flow: row wrap;
  background-color: #fff;
  border-bottom: pink;
}
@media (max-width: 370px) {
  .header-container {
    height: fit-content;
  }
}

.header-title {
  display: block;
  color: pink;
  margin: 0px;
  font-weight: 800;
  font-size: 30px;
  font-family: "Outfit", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
}